import React from "react";
import {Switch,Route,Link,Redirect} from "react-router-dom";
import { Nav, Navbar } from 'react-bootstrap';

import Home from "views/public/home.js"
import About from "views/public/about.js"
import Projects from "views/public/projects.js";
import Blogs from "views/public/blogs.js";
import Dashboard from "views/private/dashboard.js";

import Footer from "../components/Footer.js"

const Admin = () => {
  return (
    <div className="container-fluid">
      <div className="main-content">
        <Navbar bg="light" variant="light" expand="lg" sticky="top">
          <Navbar.Brand as={Link} to="/">
              <img src="/assets/img/logo.png" alt="Logo"/>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav"/>
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
              <Nav.Link as={Link} to="/" >Home</Nav.Link>
              <Nav.Link as={Link} to="/about" >About</Nav.Link>
              <Nav.Link as={Link} to="/projects">Projects</Nav.Link>
              <Nav.Link as={Link} to="/blogs">Projects</Nav.Link>
              <Nav.Link as={Link} to="/admin/dashboard">Dashboard</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <Switch>
          <Route path="/" exact component={Home}/>
          <Route path="/about" component={About}/>
          <Route path="/projects" component={Projects}/>
          <Route path="/blogs" component={Blogs}/>
          <Route path="/admin/dashboard" component={Dashboard}/>
          <Redirect from="*" to="/" />
        </Switch>
      </div>
      <Footer/>
    </div>
  );
}
export default Admin;